import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';  // For routing
import WorkflowView from './WorkflowView';  // Existing WorkflowView component
import WorkflowGanttChart from './WorkflowGanttChart';  // Gantt chart component
import Menu from './Menu';  // Import the Menu component

function App() {
  return (
    <Router>
      <div className="App">
        <Menu />  {/* Add the Menu component here */}

        <Routes>
          <Route path="/" element={<WorkflowView />} />  {/* Default home route */}
          <Route path="/react-page" element={<WorkflowGanttChart workflowId={1} />} />  {/* Gantt chart route */}
        </Routes>
      </div>
    </Router>
  );
}

export default App;
